// Contains the profit = costs - revenue
//
import React, {useState, useReducer, useEffect,memo} from 'react'
import { v4 as uuidv4 } from 'uuid';
import Paper from '@material-ui/core/Paper';
import Modal from '@material-ui/core/Modal';
import Radium from 'radium';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';



import {HOBBIES_ACTIONS} from './Hobbies';
import Costs from './Components/Costs';
import Revenue from './Components/Revenue';
import AddItemModal from './Components/addItemPopup/AddItemModal';


export const ACTIONS = {
    CHANGE_HOBBY: 'change-hobby',
    ADD_COST: 'add-cost',
    ADD_REVENUE: 'add-revenue'
}

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      width: '100%',
      backgroundColor: theme.palette.background.default,
    },
  }));

function prepend(value, array) {
    var newArray = array.slice();
    newArray.unshift(value);
    return newArray;
  }

function reducer(items, action) {
    switch (action.type) {
        case ACTIONS.CHANGE_HOBBY:
            var getItems = localStorage.getItem(action.payload.currHobby.id)
            if(!getItems){
                getItems = [[],[],{price: 0}]
            }
            else {
                getItems = JSON.parse(localStorage.getItem(action.payload.currHobby.id))
            }
            return getItems;

            //return hobbies[action.payload.hobbyIndex]
        case ACTIONS.ADD_COST:
            // return [[...items[0], add_Item(action.payload.date,action.payload.name,action.payload.price,action.payload.source)],items[1]]
            const newCost = [prepend(add_Item(action.payload.date,action.payload.name,action.payload.price,action.payload.source),items[0]),items[1],{price: +items[2].price - +action.payload.price}]
            localStorage.setItem(action.payload.id,JSON.stringify(newCost))
            return newCost
        case ACTIONS.ADD_REVENUE:
            // return [items[0],[...items[1],add_Revenue(action.payload.date,action.payload.name,action.payload.price)]]
            const newRevenue = [items[0],prepend(add_Revenue(action.payload.date,action.payload.name,action.payload.price),items[1]),{price: +items[2].price + +action.payload.price}]
            localStorage.setItem(action.payload.id,JSON.stringify(newRevenue))
            return newRevenue
        // case ACTIONS.CALCULATE_PRICE:
        //     const newPrice = 
        //     const newItem = [items[0],items[1],newPrice]
        default:
            return items;
    }
}

function add_Item(date,name,price,source) {
    return {
        id: uuidv4(),
        date: date,
        name: name,
        price: price,
        source: source

    }
}

function add_Revenue(date,name,price) {
    return {
        id: uuidv4(),
        date: date,
        name: name,
        price: price

    }
}

function Hobby(props) {
    //localStorage.setItem('testItems',JSON.stringify(testItems));

    //const initial = hobbies[props.value]

    var getItems = JSON.parse(localStorage.getItem(props.currHobby.id))
    if(!getItems){
        getItems = [[],[],{price: 0}]
    }
    console.log(getItems)
    const [items, dispatch] = useReducer(reducer, getItems);
    console.log("items after reducer %o", items)
    const classes = useStyles();

    useEffect(()=>{
        dispatch({type: ACTIONS.CHANGE_HOBBY,payload:{currHobby: props.currHobby}})
    },[props.currHobby])

    return (
        <div className={classes.root} style={{height:props.hobbyHeight,position:'fixed',top:props.appBarHeight,overflowY: 'scroll'}}>
            <div style={radium_styles.tables}>
            <Paper style={radium_styles.profitParent}>
                <span style={radium_styles.profit}>Profit: {items[2].price}</span>
            </Paper>
            <Grid container spacing = {1}>
                <Grid item xs={12} sm={6}>
                    {console.log("Items is:")}
                    {console.log({items})}
                    <Costs items={items[0]}/>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Revenue items={items[1]}/>
                </Grid>
                <Grid item xs={12}>
                    <AddItemModal currHobby={props.currHobby} dispatch={dispatch}/>
                </Grid>
            </Grid>        
            </div>
        </div>
    )
}

const radium_styles = {
    profitParent: {
        //height: '50px',
        marginBottom: '5px',
        padding: '5px 0',
    },
    profit: {
        //background: '#888',
        //margin: '20px',
        fontSize: '20px',
        ':hover': {
            background: '#333',
        }
    },
    tables: {
        padding: '10px 10px',
    }
}

export default memo(Radium(Hobby))
