import React, {useState, useRef, useEffect, useReducer, memo, useCallback} from 'react';
import Hobby from './Hobby';
import { v4 as uuidv4 } from 'uuid';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

import RootRef from '@material-ui/core/RootRef';
import AddHobbyModal from './Components/addHobbyModal/AddHobbyModal'

function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`scrollable-auto-tabpanel-${index}`}
        aria-labelledby={`scrollable-auto-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box p={3}>
            <Typography>{children}</Typography>
          </Box>
        )}
      </div>
    );
  }
  
  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
  };
  
  function a11yProps(index) {
    return {
      id: `scrollable-auto-tab-${index}`,
      'aria-controls': `scrollable-auto-tabpanel-${index}`,
    };
  }
  
  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      width: '100%',
      backgroundColor: theme.palette.background.paper,
    },
    appbar: {
      backgroundColor: "#424242",
      width: "100%",
      flexDirection: "row"
    }
    }));

  const darkTheme = createMuiTheme({
    palette: {
      type: 'dark',
      primary: {main: '#b388ff'}
    },
  });
  
export const HOBBIES_ACTIONS = {
  ADD_HOBBY: "add-hobby",
  CHANGE_INDEX: "change-index",
}

function selectedHobby(hobbies, hobbyIndex){
  if(hobbies.length < 1){
    return [];
  }
  return hobbies.find((hobby)=>{
    return hobby.index === hobbyIndex;
  });
}

function getNextID(hobbies){
  if(hobbies.length < 1){
    return 0
  }
  else {
    return hobbies.reduce((prev,current)=>{
      return(prev.index > current.index) ? prev : current
    }).index + 1
  }
}

function reducer(hobbies, action) {
  console.log(action.payload.newHobby)
  switch (action.type) {
      case HOBBIES_ACTIONS.ADD_HOBBY:
          const newHobbies = [...hobbies,{id: uuidv4(),index: getNextID(hobbies),name: action.payload.newHobby}]
          localStorage.setItem('hobbies',JSON.stringify(newHobbies))
          return newHobbies
      // case HOBBIES_ACTIONS.CHANGE_INDEX:
      //     return hobbies.map((hobby)=>{
      //       if(hobby.index === action.payload.index){
      //         hobby.index = action.payload.newIndex
      //       }
      //       return hobby
      //     })
      default:
          return hobbies;
  }
}


function Hobbies(props) {
    
    const appBarRef = useRef();
    const classes = useStyles();
    // const indexRef = useRef({
    //   hobbyIndex: 1
    // });
    var hobbiesz = JSON.parse(localStorage.getItem('hobbies'));
    if(!hobbiesz){
      hobbiesz = [];
    }

    const [hobbyIndex,setHobbyIndex] = useState(0)
    const [hobbies, dispatchs] = useReducer(reducer,hobbiesz)
    const [currHobby, setCurrHobby] = useState(selectedHobby(hobbies,hobbyIndex))
    console.log("hobbies initial state is %o", hobbies)
    const [hobbyHeight,setHobbyHeight] = useState(0)
    const [appBarHeight,setAppBarHeight] = useState(0)

    //const memoizedCurrHobby = useCallback(hobby => setCurrHobby(hobby),[]);

    useEffect( ()=> {
      console.log(props.bottomNavRef.current.offsetHeight);
      console.log(appBarRef.current.offsetHeight);
      console.log(window.innerHeight-props.bottomNavRef.current.offsetHeight-appBarRef.current.offsetHeight)
      setAppBarHeight(appBarRef.current.offsetHeight)
      setHobbyHeight(window.innerHeight-props.bottomNavRef.current.offsetHeight-appBarRef.current.offsetHeight);
      
    }, []);

    useEffect( () => {
      console.log("Rerender currHobby")
      setCurrHobby(selectedHobby(hobbies,hobbyIndex))
    }, [hobbyIndex,hobbies])

    const handleChange = (event, newValue) => {
        setHobbyIndex(newValue);
    };

    return (
      <ThemeProvider theme={darkTheme}>
        <div className={classes.root}>
            <RootRef rootRef={appBarRef}>
              <AppBar position="fixed" className={classes.appbar}>
                  <AddHobbyModal dispatchs={dispatchs}/>
                  <Tabs
                      value={hobbyIndex}
                      onChange={handleChange}
                      indicatorColor="primary"
                      textColor="primary"
                      variant="scrollable"
                      scrollButtons="auto"
                      aria-label="scrollable tabs"
                  >
                  {hobbies.map((hobby) => (
                    <Tab key = {hobby.id} label={hobby.name} {...a11yProps({hobby}.index)}/>
                  ))} 
                  </Tabs>
              </AppBar>
            </RootRef>
            
            
            <Hobby currHobby={currHobby} hobbyHeight={hobbyHeight} appBarHeight={appBarHeight} dispatchs={dispatchs}/>

        </div>
      </ThemeProvider>
    )
}


export default memo(Hobbies)
