import React, { memo } from 'react';
import { makeStyles, ThemeProvider } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import FolderIcon from '@material-ui/icons/Folder';
import RestoreIcon from '@material-ui/icons/Restore';
import FavoriteIcon from '@material-ui/icons/Favorite';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import HomeIcon from '@material-ui/icons/Home';
import PersonIcon from '@material-ui/icons/Person';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
import { createMuiTheme } from '@material-ui/core/styles';

const useStyles = makeStyles((theme)=>({
  root: {
    width: 'inherit',
    height: '70px',
    position: 'fixed',
    bottom: '0',
    backgroundColor: "#424242",
    width: "100%",
    flexDirection: "row",
    //backgroundColor: theme.palette.background.default,
  },
}));

// const useStyles = makeStyles((theme) => ({
//   root: {
//     flexGrow: 1,
//     width: '100%',
//     backgroundColor: theme.palette.background.paper,
//   },
//   }));

const darkTheme = createMuiTheme({
  palette: {
    type: 'dark',
    primary: {main: '#b388ff'}
  },
});


function BottomNav() {
  const classes = useStyles();
  const [value, setValue] = React.useState('recents');

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <ThemeProvider theme={darkTheme}>
    <BottomNavigation value={value} onChange={handleChange} className={classes.root} color="default">
      <BottomNavigationAction label="Home" value="home" icon={<HomeIcon />} />
      <BottomNavigationAction label="Cost" value="cost" icon={<AttachMoneyIcon />} />
      <BottomNavigationAction label="Favorite" value="favorite" icon={<FavoriteIcon />} />
      <BottomNavigationAction label="Bio" value="bio" icon={<PersonIcon />} />
    </BottomNavigation>
    </ThemeProvider>
  );
}

export default memo(BottomNav)