import React, {useState, memo} from 'react'
import {Text, View, StyleSheet} from 'react-native';
import Radium from 'radium';
import Input from '@material-ui/core/Input';
import TextField from '@material-ui/core/TextField';
import { StylesProvider } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import {ACTIONS} from '../../Hobby';

import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

function AddPrice(props) {
    const [itemDate, setItemDate] = useState(Date.now())
    const [itemName, setItemName] = useState('')
    const [itemPrice, setItemPrice] = useState('')
    const [itemSource, setItemSource] = useState('')

    function add_Cost() {
        console.log("currID is %o",props.currHobby)
        props.dispatch({type: ACTIONS.ADD_COST, payload:{date: itemDate,name:itemName, price:itemPrice, source:itemSource, id:props.currHobby.id}})
        setItemName('')
        setItemPrice('')
    }

    function add_Revenue() {
        props.dispatch({type: ACTIONS.ADD_REVENUE, payload:{date: itemDate,name:itemName,price:itemPrice,id:props.currHobby.id}})
    }

    return (
        <div style={{color:'white'}}>
            <View style={styles.title}>
                <p>Add Item</p>
            </View>
            <form> 
                <View style={styles.eachLine}>
                    <View style={styles.label}>
                        <p>Date:</p>
                    </View>
                    <TextField id="outlined-basic" variant="outlined" style={styles.textFieldStyle} size="small" value={itemDate} onChange={e=>setItemDate(e.target.value)}/>
                </View>
                <View style={styles.eachLine}>
                    <View style={styles.label}>
                        <p>Name:</p>
                    </View>
                    <TextField id="outlined-basic" variant="outlined" size="small" value={itemName} onChange={e=>setItemName(e.target.value)}/>
                </View>
                <View style={styles.eachLine}>
                    <View style={styles.label}>
                        <p>Price:</p>
                    </View>
                    <TextField id="outlined-basic" type="number" variant="outlined" size="small" value={itemPrice} onChange={e=>setItemPrice(e.target.value)}/>
                </View>
                <View style={styles.eachLine}>
                    <View style={styles.label}>
                        <p>Source:</p>
                    </View>
                    <TextField id="outlined-basic" variant="outlined" size="small" value={itemSource} onChange={e=>setItemSource(e.target.value)} onClick={()=>{setItemSource('')}}/>
                </View>
                <View style={styles.submitButtons}>
                    <Button variant="contained" style={{margin: '10px'}} color="primary" size="small" onClick={add_Cost}>Add Cost</Button>
                    <Button variant="contained" style={{margin: '10px'}} color="primary" size="small" onClick={add_Revenue}>Add Revenue</Button>
                </View>
            </form>

        </div>
    )
}

const styles = StyleSheet.create({
    addPrice: {
        paddingTop: '5px',
    },
    textRight: {
        textAlign: 'right',
    },
    sameLine: {
        display: 'flex',
        flexDirection: 'row',
    },
    label: {
        justifyContent:'end',
        bottom: '7px',
        width: '40px',
        marginRight: '30px',
    },
    title: {
        //marginBottom: '10px',
        paddingBottom: '20px',
        height: '40px',
        flexDirection: 'row',
        justifyContent: 'center',
    },
    eachLine: {
        //display: 'flex',
        margin: '10px',
        flexDirection: 'row',
        justifyContent: 'center',
    },
    submitButtons: {
        margin: '5px',
        flexDirection: 'row',
        justifyContent: 'center',
    },
});

export default memo(AddPrice)