import React,{memo, useState} from 'react'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import {HOBBIES_ACTIONS} from '../../Hobbies'

function AddHobby({dispatchs}) {

    const [newHobby,setNewHobby] = useState('')

    function add_hobby() {
        dispatchs({type: HOBBIES_ACTIONS.ADD_HOBBY, payload:{newHobby: newHobby}})
        
    }

    return (
        <div style={{textAlign: 'center'}}>
            <TextField id="outlined-basic" variant="outlined" size="small" value={newHobby} onChange={e=>setNewHobby(e.target.value)}/>
            <Button variant="contained" style={{margin: '10px'}} color="primary" size="small" onClick={add_hobby}>Add Hobby</Button>
        </div>
    )
}
export default memo(AddHobby)
