import React, {useRef} from 'react';
import './App.css';
import Hobbies from './Hobbies';
import RootRef from '@material-ui/core/RootRef';

import BottomNav from './Components/BottomNav/BottomNav';

function App() {

  const bottomNavRef = useRef();

  const haubi = localStorage.getItem('haubi')
  if(!haubi){
    console.log("empty");
  }

  return (
    <div className="App">
      <Hobbies bottomNavRef={bottomNavRef}/>
      <RootRef rootRef={bottomNavRef}>
        <BottomNav/>
      </RootRef>

    </div>
  );
}

export default App;
